<template>
  <div class="hello">
      <el-radio-group v-model="radio" @change="change">
        <el-radio v-for="(value,key,i) in data[index]" :key="i" v-model="radio" :label="key">{{key}}:  {{value}}</el-radio>
      </el-radio-group>
    <el-button @click="prev">上一题</el-button>
    <el-button @click="next">下一题</el-button>
    <p v-for="(item,i) in selected" :key="i">你选择的第{{i+1}}道题答案为{{item}}</p>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      data: [{
        A:'我是第一道题选项A的内容',
        B:'我是第一道题选项B的内容',
        C:'我是第一道题选项C的内容',
        D:'我是第一道题选项D的内容'
      },{
        A:'我是第二道题选项A的内容',
        B:'我是第二道题选项B的内容',
        C:'我是第二道题选项C的内容',
        D:'我是第二道题选项D的内容'
      },{
        A:'我是第三道题选项A的内容',
        B:'我是第三道题选项B的内容',
        C:'我是第三道题选项C的内容',
        D:'我是第三道题选项D的内容'
      }],
      index:0,
      selected:[],
      radio:''
    }
  },
  methods:{
    change(val){
      this.selected[this.index] = val;
      this.selected = this.selected.concat();
    },
    next(){
      if(this.index < this.data.length){
        this.index++;
        this.radio = this.selected[this.index] || '';
      }else{
        console.log(this.index,this.data.length);
        alert('已经是最后一题了');
      }
    },
    prev(){
      if(this.index > 0){
        this.index--;
        this.radio = this.selected[this.index] || '';
      }else{
        alert('已经是第一题了');
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-radio,.el-radio-group{
    display: block;
  }
  .el-radio:last-child{
    margin-right: 30px;
  }
</style>



